@import 'vars';


html, body, #root, .app {
    width: 100vw;
    margin: 0;
    overflow-x: hidden;

}

h1 {
    font-weight: 300;
    font-size: 2em;
}
 h2 {
    font-weight: 300;
    font-size: 1.5em;
}
h3 {
    font-weight: 500;
    font-size: 1.25em;
}
h4 {
    font-weight: 500;
    font-size: 1em;
}
h5 {
    font-weight: 700;
    font-size: 0.75em;
} 
h6 {
    font-weight: 700;
    font-size: 0.75em;
    text-transform: uppercase;
}

a:link {
    color: $cd-blue-700
}
a:visited {
    color: $cd-blue-900
}
a:hover, a:active {
    color: $cd-blue-500
}

::selection {
    background-color: $cd-blue-200;
}

html {
    font-family: Roboto, sans-serif;
    color: #000;

    $fore: #FAFAFC;
    $back: #FFF;


    background-color: #000;
    background-image:
    radial-gradient(circle at 100% 150%, $fore 24%, $back 24%, $back 28%, $fore 28%, $fore 36%, $back 36%, $back 40%, transparent 40%, transparent),
    radial-gradient(circle at 0    150%, $fore 24%, $back 24%, $back 28%, $fore 28%, $fore 36%, $back 36%, $back 40%, transparent 40%, transparent),
    radial-gradient(circle at 50%  100%, $back 10%, $fore 10%, $fore 23%, $back 23%, $back 30%, $fore 30%, $fore 43%, $back 43%, $back 50%, $fore 50%, $fore 63%, $back 63%, $back 71%, transparent 71%, transparent),
    radial-gradient(circle at 100% 50%, $back 5%, $fore 5%, $fore 15%, $back 15%, $back 20%, $fore 20%, $fore 29%, $back 29%, $back 34%, $fore 34%, $fore 44%, $back 44%, $back 49%, transparent 49%, transparent),
    radial-gradient(circle at 0    50%, $back 5%, $fore 5%, $fore 15%, $back 15%, $back 20%, $fore 20%, $fore 29%, $back 29%, $back 34%, $fore 34%, $fore 44%, $back 44%, $back 49%, transparent 49%, transparent);
    background-size: 200px 100px;
}

body {
    position: relative;
}

.centerer {
    display: flex;
    align-items: center;
    justify-content: center;
}
