html,body,#root,.app{width:100vw;margin:0;overflow-x:hidden}h1{font-weight:300;font-size:2em}h2{font-weight:300;font-size:1.5em}h3{font-weight:500;font-size:1.25em}h4{font-weight:500;font-size:1em}h5{font-weight:700;font-size:0.75em}h6{font-weight:700;font-size:0.75em;text-transform:uppercase}a:link{color:#284E87}a:visited{color:#0F255A}a:hover,a:active{color:#5086bc}::selection{background-color:#BFE4F8}html{font-family:Roboto, sans-serif;color:#000;background-color:#000;background-image:radial-gradient(circle at 100% 150%, #FAFAFC 24%, #fff 24%, #fff 28%, #FAFAFC 28%, #FAFAFC 36%, #fff 36%, #fff 40%, transparent 40%, transparent),radial-gradient(circle at 0 150%, #FAFAFC 24%, #fff 24%, #fff 28%, #FAFAFC 28%, #FAFAFC 36%, #fff 36%, #fff 40%, transparent 40%, transparent),radial-gradient(circle at 50% 100%, #fff 10%, #FAFAFC 10%, #FAFAFC 23%, #fff 23%, #fff 30%, #FAFAFC 30%, #FAFAFC 43%, #fff 43%, #fff 50%, #FAFAFC 50%, #FAFAFC 63%, #fff 63%, #fff 71%, transparent 71%, transparent),radial-gradient(circle at 100% 50%, #fff 5%, #FAFAFC 5%, #FAFAFC 15%, #fff 15%, #fff 20%, #FAFAFC 20%, #FAFAFC 29%, #fff 29%, #fff 34%, #FAFAFC 34%, #FAFAFC 44%, #fff 44%, #fff 49%, transparent 49%, transparent),radial-gradient(circle at 0 50%, #fff 5%, #FAFAFC 5%, #FAFAFC 15%, #fff 15%, #fff 20%, #FAFAFC 20%, #FAFAFC 29%, #fff 29%, #fff 34%, #FAFAFC 34%, #FAFAFC 44%, #fff 44%, #fff 49%, transparent 49%, transparent);background-size:200px 100px}body{position:relative}.centerer{display:flex;align-items:center;justify-content:center}

.stripes{position:absolute;left:0;top:0;bottom:0;display:flex}.stripes .s1,.stripes .s2,.stripes .s3{box-shadow:0 0 0.4em #09142D;transition:flex 0.2s;flex:0 0 .8em}.stripes .s1:hover,.stripes .s2:hover,.stripes .s3:hover{flex:0 0 1.2em}.stripes .s1{background-color:#7AADD6;z-index:1}.stripes .s2{background-color:#3A68A1;z-index:2}.stripes .s3{background-color:#19366D;z-index:3}.header-container{position:relative;background-color:#F4F4F6;box-shadow:0em 0.5em 1em #999;margin:0 2em;border-radius:0 2em 2em 0;overflow:hidden;max-width:100%;width:100%}.header{margin-left:4em;margin-right:1em;display:grid;flex:1 0 auto;max-width:100%}@media only screen and (max-width: 768px){.header{grid-template:"name" "email" "twitter" / auto}}@media only screen and (min-width: 768px){.header{grid-template:"name email twitter" / auto auto auto}}.header *{justify-self:left;align-self:center}

