@import '../vars';

.stripes {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    display: flex;

    .s1, .s2, .s3 {
        box-shadow: 0 0 0.4em $cd-blue-1000;
    
        transition: flex 0.2s;
        flex: 0 0 .8em;
    
        &:hover {
            flex: 0 0 1.2em;
        }
    }
    
    .s1 {
        background-color: $cd-blue-400;
        z-index: 1;
    }
    
    .s2 {
        background-color: $cd-blue-600;
        z-index: 2;
    }
    
    .s3 {
        background-color: $cd-blue-800;
        z-index: 3;
    }
}

.header-container {
    position: relative;
    background-color: $cd-white;
    box-shadow: 0em 0.5em 1em #999;

    margin: 0 2em;

    border-radius: 0 2em 2em 0;

    overflow: hidden;
    max-width: 100%;
    width: 100%
}

.header {
    margin-left: 4em;
    margin-right: 1em;
    display: grid;

    @media only screen and (max-width: 768px) {
        grid-template:
            "name"
            "email"
            "twitter"
            / auto
    }   
    
    @media only screen and (min-width: 768px) {
        grid-template:
            "name email twitter"
            / auto auto auto
    }

    * {
        justify-self: left;
        align-self: center;
    }

    flex: 1 0 auto;
    max-width: 100%;
}